*, *::before, *::after {
  box-sizing: border-box;
  font-family: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
}

html:focus-within {
  scroll-behavior: smooth;
}

a, a:visited, a:hover {
  text-decoration: none;
  color: inherit;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  border: none;
  color: inherit;
  cursor: pointer;
}

body {
  font-family: var(--ff);
  font-size: var(--fs-body);
  line-height: 1.5;
}

.container {
  max-width: 69.375rem;
  margin: 0 auto;
  padding: 1.5625rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    width: 100vw;
    height: 100vh;
  }
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}